import axios from 'axios'
import React, { useEffect, useState } from 'react'
import hostlink from '../ApiConfig/hostlinl'
import { Link } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import DataTable from 'react-data-table-component';
const ContactList = () => {
    const [cont, setCont] = useState([])
    const [search, setsearch] = useState([])


    const deletecontact = async (e) => {
        try {
            await axios.delete(`${hostlink}/Contact/${e}`)
            toast('Delete Sucessfull !');
            setTimeout(() => {
                window.location.assign('/ContactList');
            }, 1000); // Adjust the timeout value as needed
        } catch (error) {
            toast.error('Failed to delete contact. Please try again later.');
        }
    };
    const columns = [
        { name: 'ID', selector: row => row.id, cell: (row, index) => <div>{index + 1}</div>, "width": "6%" },
        { name: 'Name', sortable: true, selector: row => row.name, },
        { name: 'Email', sortable: true, selector: row => row.email, "width": "20%" },
        { name: 'Address', sortable: true, selector: row => row.address, "width": "30%" },
        { name: 'Mobile', sortable: true, selector: row => row.mob, "width": "10%" },
        { name: 'Pincode', sortable: true, selector: row => row.pin, "width": "8%" },
        {
            name: 'Actions', "width": "10%", cell: (row) => (<div>
                <Link to={`/ContactListEdit/${row.id}`} className='btn btn-sm btn-outline-dark'>Edit</Link> &nbsp;
                <Link onClick={() => deletecontact(`${row.id}`)} className='btn btn-sm btn-danger'>Delete</Link>
            </div>), allowOverflow: true, button: true,
        },

    ];

    useEffect(() => {
        axios.get(`${hostlink}/Contacts`)
            .then((Response) => {
                setCont(Response.data)
                console.log("Data fatched success")
            })
            .catch(() => {
                console.log("Data Not Fatching from server")
            })
    }, [])

    return (
        <div>

            <br /><br />
            <div className='container-fluid mt-1'>

                <dir style={{ display: "flex" }} className='row'>
                    <div className='col-md-5'>
                        <h1><i class="fa-regular fa-address-book"></i> Contact List</h1>
                    </div>
                    <div style={{ outline: "none", height: "40px", marginTop: "8px", backgroundColor: "rgb(248,249,250)", border: "1px solid black", justifyContent: "space-around", alignItems: "center", textAlign: "center", borderRadius: "10px", display: "flex" }} className='col-md-3'>
                        <i style={{ outline: "none", marginTop: "1px", border: "0px solid black", justifyContent: "space-around", borderRadius: "10px", display: "flex" }} class="fa-solid fa-magnifying-glass"></i> &nbsp;&nbsp;&nbsp;
                        <input style={{ outline: "none", marginTop: "1px", backgroundColor: "rgb(248,249,250)", border: "0px solid black", justifyContent: "space-around", borderRadius: "10px", display: "flex" }} className='w-100 h-75' type="text" name='search' value={search} placeholder='Search Name Mobile Address' />
                    </div>

                    <div className='col-md-4 text-end'>
                        <h1> <Link to={"/NewContact"} className='btn btn-outline-primary'>Create New</Link> </h1>
                    </div>
                </dir>


                <DataTable
                    columns={columns}
                    data={cont}
                    pagination
                    fixedHeader
                    fixedHeaderScrollHeight='530px'
                />

            </div>
            <ToastContainer autoClose={1000}></ToastContainer>
        </div>
    )
}

export default ContactList
