import axios from 'axios';
import React, { useState } from 'react'
import hostlink from '../ApiConfig/hostlinl';
import { Link } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const NewContact = () => {

  const [data, setData] = useState({
    name: "",
    email: "",
    address: "",
    mob: "",
    pin: ""
  })

  const { name, email, address, mob, pin } = data;

  const OnInputchange = (e) => {
    console.log(hostlink)
    setData({ ...data, [e.target.name]: e.target.value })
  }

  const Savedata = async (e) => {
    try {
      await axios.post(`${hostlink}/Contact`, data)
      .then((Response) => {
        toast.success("Save Sucessfull !")
        setTimeout(() => {
            window.location.assign('/ContactList');
          }, 1000); // Adjust the timeout value as needed
      })
      .catch((Response) => {
        console.log(Response.data)
      })
      
    } catch (error) {
      
    }
      
  }
  return (
    <div className='container '>
      <br /><br /><br />
      <h4 className='font-monospace text-danger'><i class="fa-regular fa-address-book"></i> New Contact Details  </h4>
      <form class="row g-3" onSubmit={Savedata}>
        <div class="col-md-6">
          <label for="inputname" class="form-label">Name</label>
          <input type="text" onChange={(e) => OnInputchange(e)} class="form-control" name='name' value={name} id="inputname" placeholder='Name' />
        </div>
        <div class="col-md-6">
          <label for="inputEmail4" class="form-label">Email</label>
          <input type="email" onChange={(e) => OnInputchange(e)} class="form-control" name='email' value={email} id="inputEmail4" placeholder='Email' />
        </div>
        <div class="col-md-12">
          <label for="inputAddress" class="form-label">Address</label>
          <input type="text" onChange={(e) => OnInputchange(e)} class="form-control" name='address' value={address} id="inputAddress" placeholder="Address" />
        </div>
        <div class="col-md-6">
          <label for="inputnumber" class="form-label">Mobile No</label>
          <input type="text" onChange={(e) => OnInputchange(e)} class="form-control" name='mob' value={mob} id="inputnumber" placeholder='Mobile No' />
        </div>
        <div class="col-md-6">
          <label for="inputZip" class="form-label">Pin Code</label>
          <input type="text" onChange={(e) => OnInputchange(e)} class="form-control" name='pin' value={pin} id="inputZip" placeholder='Pin Code'/>
        </div>

        <div class="col-6">
          <Link type="submit" onClick={Savedata} class="btn btn-primary">Save</Link>  &nbsp;
          <Link to={"/ContactList"} type="submit"  class="btn btn-primary">Cancel</Link>
        </div>
      </form>
      <ToastContainer autoClose={1000}></ToastContainer>
    </div>
  )
}

export default NewContact
